.product-detail-page {
  padding: 2rem 0;
}

.product-gallery {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.main-image {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  cursor: zoom-in;
}

.main-image img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.main-image:hover img {
  transform: scale(1.05);
}

.thumbnail-images {
  display: flex;
  gap: 0.5rem;
  overflow-x: auto;
  padding: 0.5rem 0;
}

.thumbnail {
  width: 80px;
  height: 80px;
  border: 2px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  flex-shrink: 0;
  transition: all 0.2s ease;
}

.thumbnail:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.thumbnail.active {
  border-color: #007bff;
  transform: translateY(-5px);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
}

.zoom-container {
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.zoom-lens {
  position: absolute;
  border: 2px solid #000;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: zoom-in;
  display: none;
}

.zoom-result {
  position: absolute;
  top: 0;
  left: 100%;
  width: 400px;
  height: 400px;
  border: 1px solid #ccc;
  background-repeat: no-repeat;
  background-color: white;
  display: none;
  z-index: 1000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.product-info {
  padding: 0 1rem;
}

.product-title {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.product-rating {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.rating-count {
  color: #6c757d;
  font-size: 0.9rem;
}

.product-price {
  margin-bottom: 1rem;
}

.current-price {
  font-size: 1.8rem;
  font-weight: bold;
  color: #28a745;
}

.original-price {
  font-size: 1.2rem;
  color: #6c757d;
  text-decoration: line-through;
  margin-left: 0.5rem;
}

.product-description {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.product-meta {
  margin-bottom: 1.5rem;
}

.meta-item {
  display: flex;
  margin-bottom: 0.5rem;
}

.meta-label {
  font-weight: bold;
  width: 120px;
}

.meta-value.in-stock {
  color: #28a745;
  font-weight: bold;
}

.product-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.quantity-selector {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.quantity-controls {
  display: flex;
  align-items: center;
}

.quantity-controls button {
  width: 36px;
  height: 36px;
  padding: 0;
}

.quantity {
  min-width: 40px;
  text-align: center;
  font-weight: bold;
}

.add-to-cart-btn, .wishlist-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.product-share {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.share-icons {
  display: flex;
  gap: 0.5rem;
}

.share-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #f8f9fa;
  color: #495057;
  text-decoration: none;
  transition: all 0.2s;
}

.share-icon:hover {
  background-color: #007bff;
  color: white;
}

.product-tabs {
  margin-top: 2rem;
}

.reviews-summary {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.average-rating {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.rating-value {
  font-size: 3rem;
  font-weight: bold;
  color: #007bff;
}

.review-count {
  color: #6c757d;
}

.review-form {
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.review-item {
  padding: 1rem 0;
  border-bottom: 1px solid #dee2e6;
}

.review-item:last-child {
  border-bottom: none;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.review-rating {
  display: flex;
  gap: 0.25rem;
}

.review-date {
  color: #6c757d;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.review-text {
  line-height: 1.6;
}

@media (max-width: 768px) {
  .product-info {
    padding: 1rem 0;
  }
  
  .product-actions {
    flex-direction: column;
  }
  
  .add-to-cart-btn, .wishlist-btn {
    width: 100%;
    justify-content: center;
  }
  
  .main-image img {
    height: 300px;
  }
  
  .zoom-result {
    width: 300px;
    height: 300px;
    left: 50%;
    transform: translateX(-50%);
    top: 100%;
  }
  
  .product-title {
    font-size: 1.5rem;
  }
  
  .current-price {
    font-size: 1.5rem;
  }
  
  .thumbnail {
    width: 60px;
    height: 60px;
  }
  
  .product-meta .meta-item {
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .meta-label {
    width: auto;
  }
}

@media (max-width: 480px) {
  .product-detail-page {
    padding: 1rem 0;
  }
  
  .main-image img {
    height: 250px;
  }
  
  .thumbnail {
    width: 50px;
    height: 50px;
  }
  
  .product-title {
    font-size: 1.3rem;
  }
  
  .current-price {
    font-size: 1.3rem;
  }
  
  .product-actions {
    gap: 0.5rem;
  }
  
  .quantity-controls button {
    width: 30px;
    height: 30px;
  }
  
  .quantity {
    min-width: 30px;
  }
  
  .share-icons {
    flex-wrap: wrap;
  }
}

/* Touch device optimizations */
@media (hover: none) and (pointer: coarse) {
  .thumbnail {
    width: 70px;
    height: 70px;
  }
  
  .thumbnail:hover {
    transform: none;
  }
  
  .thumbnail.active {
    transform: translateY(-5px);
  }
}

/* Orientation changes */
@media (orientation: landscape) and (max-height: 500px) {
  .main-image img {
    height: 200px;
  }
  
  .zoom-result {
    width: 200px;
    height: 200px;
  }
}